@use "../base/variables" as v;
@use "../base/mixins" as m;

.gallery {
    
    position: relative;
    padding: 5rem 0;
    h3 {
        text-align: center;
    }
}
.gallery-images {
    
    @include m.reset_list;
    @include m.grid(2, 2rem);

    @include m.phone {
        @include m.grid(3, 2rem);
    }
    @include m.tablet {
        @include m.grid(4, 2rem);
    }
    @include m.desktop {
        @include m.grid(6, 2rem);
    }
}

.overlay {
    visibility: hidden;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    display: grid;
    place-items: center;
    background-color: rgb(0 0 0 /0.5);
    .img {
        display: block;
        max-width: 80rem;
    }
    .btn-close {
        width: 8rem;
        height: 8rem;
        border-radius: 50%;
        border-color: v.$pink;
        font-size: 5rem;
        background-color: v.$white;
        color: v.$pink;
        font-weight: 700;
        &:hover {
            cursor: pointer;
        }
    }
}
.show {
    visibility: visible;
    
}
